@import '../component/include';
 
/* ***** MAIN ***** */
/* ** 긴급공지 **/
.main-urgent-wrap {
    background-color: #F0F0F0;
    .urgent-ban-list {
        @include flex-layout($fd:column, $ai:normal);
        gap: var(--#{$prefix}spacer-6);
        width: 100%;
        max-width: 84.6rem;
        padding: var(--#{$prefix}spacer-6);
        margin: 0 auto;
        .urgent-ban {
            @include flex-layout($ai:center);
            gap: var(--#{$prefix}spacer-4);
            height: 8rem;
            padding: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-4);
            border-radius: var(--#{$prefix}rd-6);
            background-color: var(--#{$prefix}white);
        }
        .urgent-badge {
            @include flex-layout($ai:center);
            gap: var(--#{$prefix}spacer-1);
            font-size: var(--#{$prefix}fz-body-sm);
            font-weight: map-get($fws, bold);
            color: var(--#{$prefix}white);
            min-width: 7.8rem;
            height: 4.8rem;
            padding: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-3);
            border-radius: 10rem;
            &::before {
                display: inline-flex;
                content:'';
                width: 2.4rem;
                height: 2.4rem;
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;
            }
            &.danger {
                background-color: var(--#{$prefix}danger);
                &::before {
                    background-image: url(#{$url}/component/common/ico_urgent_badge_danger.svg);
                }
            }
            &.ok {
                background-color: var(--#{$prefix}success);
                &::before {
                    background-image: url(#{$url}/component/common/ico_urgent_badge_ok.svg);
                }
            }
            &.info {
                background-color: var(--#{$prefix}primary-70);
                &::before {
                    background-image: url(#{$url}/component/common/ico_urgent_badge_info.svg);
                }
            }
        }
        .urgent-txt {
            flex: 1;
            font-weight: map-get($fws, bold);
            @include ellipsis(2);
        }
    }
}


/* ** 비주얼  영역 ** */
.main-vban-wrap {
    &.bg {
        background-color: #D8E4F2;
    }
    &:not(.bg) {
        margin-top: var(--#{$prefix}spacer-12);
    }

    //배너 슬라이드
    .vb-swiper {
        position: relative;
        padding: var(--#{$prefix}spacer-8) var(--#{$prefix}spacer-25);
        .swiper-slide {
            height:49.6rem;
            padding: var(--#{$prefix}spacer-8) var(--#{$prefix}spacer-11);
            .in {
                @include flex-layout($jc:center, $ai: center);
                gap: var(--#{$prefix}spacer-10);
                height: 100%;
                background-position: center right;
                background-repeat: no-repeat;
            }
            .text {
                flex: 1;
                @include flex-layout($fd:column,);
                gap: var(--#{$prefix}spacer-10);
                min-height: 30rem;
                .tit {
                    font-size: var(--#{$prefix}fz-display-sm);
                    font-weight: map-get($fws, bold);
                }
                .txt {
                    font-size: var(--#{$prefix}fz-body-lg);
                }
            }
            .im {
                flex-basis: 35%;
                flex-shrink: 0;
                margin-right: auto;
                text-align: right;
                img {
                    width: 100%;
                    max-width: 34rem;
                }
            }
        }
        .swiper-button-prev,
        .swiper-button-next {
            width: 6.4rem;
            height: 6.4rem;
            &::after {
                width: 3.6rem;
                height: 3.6rem;
            }
        }
    }

    //배너 카드
    .vb-card {
        position: relative;
        z-index: 1;
        padding: var(--#{$prefix}spacer-22) var(--#{$prefix}spacer-20);
        border-radius: var(--#{$prefix}rd-12);
        background-color: var(--#{$prefix}secondary-5);
        .in {
            @include flex-layout($jc:center,);
            gap: var(--#{$prefix}spacer-10);
            height: 100%;
            background-position: center right;
            background-repeat: no-repeat;
        }
        .main-d-text {
            flex: 1;
            @include flex-layout($fd:column,);
            gap: var(--#{$prefix}spacer-10);
            min-height: 30rem;
            .tit {
                font-size: var(--#{$prefix}fz-display-sm);
                font-weight: map-get($fws, bold);
            }
            .txt {
                font-size: var(--#{$prefix}fz-body-lg);
                // font-weight: map-get($fws, bold);
                // color: var(--#{$prefix}gray-90);
            }
        }
        .im {
            flex-basis: 35%;
            flex-shrink: 0;
            margin-right: auto;
            text-align: right;
            img {
                width: 100%;
                max-width: 30rem;
            }
        }
        .main-d-links {
            flex-direction: row;
            position: absolute;
            width: 100%;
            left:0;
            bottom: 0;
            z-index: 2;
            padding: var(--#{$prefix}spacer-10) var(--#{$prefix}spacer-20);
            .btn {
                font-weight: map-get($fws, bold);
                height:8rem;
                padding: var(--#{$prefix}spacer-5) var(--#{$prefix}spacer-6) !important;
            }
        }
    }

    //상단 검색
    .main-top-sch {
        @include flex-layout($fd:column,$jc:center, $ai:normal);
        gap: var(--#{$prefix}spacer-20);
        width: 100%;
        max-width: 68.5rem;
        padding: var(--#{$prefix}spacer-16) 0;
        margin: 0 auto;
        .sch-tit {
            font-size: var(--#{$prefix}fz-title-xlg);
            font-weight: map-get($fws, bold);
            text-align: center;
        }
        .sch-form-wrap {
            gap: var(--#{$prefix}spacer-20);
        }
        .sch-tag {
            @include flex-layout();
            gap: var(--#{$prefix}spacer-6);
            dt {
                display: inline-flex;
                align-items: center;
                flex-shrink: 0;
                font-weight: map-get($fws, bold);
                line-height: 1;
                height: 2.4rem;
            }
            dd {
                @include flex-layout();
                gap: var(--#{$prefix}spacer-2);
                overflow-x: auto;
                .krds-badge {
                    white-space: nowrap;
                }
            }
        }
    }

    //상단 베이직
    #main-vb-basic {
        height: 48rem;
        .d-auto {
            .in {
                @include flex-layout($fd: column, $ai:normal);
                > div {
                    width: 100%;
                    max-width: 68.5rem;
                    margin: 0 auto;
                }
            }
            .sch-form-wrap {
                align-items: center;
                padding: var(--#{$prefix}spacer-13) 0;
                .sch-input {
                    width: 100%;
                    max-width: 56rem;
                }
                .sch-ranking {
                    #sch-ranking-list {
                        .swiper {
                            height: 2.5rem;
                        }
                        .swiper-wrapper {
                            counter-reset: item;
                        }
                        .swiper-slide {
                            @include flex-layout($ai: center,);
                            counter-increment: item;
                            &::before {
                                display: inline-flex;
                                content: counter(item);
                                font-weight: map-get($fws, bold);
                                color: var(--#{$prefix}primary);
                                width: 2rem;
                            }
                            .ranking-txt {
                                width: 18.5rem;
                                @include ellipsis(1);
                            }
                            .ranking-state {
                                @include flex-layout($d: inline-flex, $jc: center, $ai: center,);
                                gap:.2rem;
                                font-size: var(--#{$prefix}fz-body-sm);
                                font-size: var(--#{$prefix}fz-body-sm);
                                width: 3.2rem;
                                &::before {
                                    display: inline-flex;
                                    content: '';
                                    width: 1rem;
                                    height: 1rem;
                                }
                                &.up {
                                    color: var(--#{$prefix}danger);
                                    &::before {
                                        width: 0;
                                        height: 0;
                                        border-bottom: .6rem solid var(--#{$prefix}danger);
                                        border-left: .4rem solid transparent;
                                        border-right: .4rem solid transparent;
                                    }
                                }
                                &.down {
                                    color:var(--#{$prefix}information);
                                    &::before {
                                        width: 0;
                                        height: 0;
                                        border-top: .6rem solid var(--#{$prefix}information);
                                        border-left: .4rem solid transparent;
                                        border-right: .4rem solid transparent;
                                    }
                                }
                                &.same {
                                    &::before {
                                        width:.6rem;
                                        height:.2rem;
                                        background-color:var(--#{$prefix}gray-90);
                                    }
                                }
                            }
                        }
                    }
                    .dl {
                        @include flex-layout();
                        gap: var(--#{$prefix}spacer-6);
                        dt {
                            font-weight: map-get($fws, bold);
                        }
                        dd {
                            @include flex-layout();
                            gap: var(--#{$prefix}spacer-1);
                        }
                    }
                }
            }
            #main-vb-btm {
                max-width: 68.5rem;
                margin: auto auto 0;
                .d-card {
                    gap: var(--#{$prefix}spacer-10);
                }
                .main-d-text {
                    gap: var(--#{$prefix}spacer-4);
                    .tit {
                        font-size: var(--#{$prefix}fz-title-md);
                    }
                }
                .im {
                    @include flex-layout($jc:center, $ai:center);
                    flex-shrink: 0;
                    width: 24rem;
                    border-radius: var(--#{$prefix}rd-12);
                    background-color: #DFE7EF;
                    img {
                        max-width: 100%;
                        max-height: 100%;
                    }
                }
                .swiper-indicator {
                    margin-top: var(--#{$prefix}spacer-6);
                }
            }
        }
        .d-fix {
            .in {
                @include flex-layout($fd: column, $ai: normal);
                gap: var(--#{$prefix}spacer-6);
            }
            .d-login-wrap {
                @include flex-layout($fd: column, $jc:center, $ai: normal);
                gap: var(--#{$prefix}spacer-6);
                height: 100%;
                .txt {
                    font-size: var(--#{$prefix}fz-title-md);
                    font-weight: map-get($fws, bold);
                    text-align: center;
                    padding-bottom: var(--#{$prefix}spacer-10);
                }
                .login-links {
                    @include flex-layout($ai: center, $jc: center);
                    > li {
                        .btn {
                            &.btn-txt {
                                height: auto;
                                padding: 0 1rem;
                            }
                        }
                        & + li {
                            position: relative;
                            margin-left: var(--#{$prefix}spacer-4);
                            padding-left: calc(var(--#{$prefix}spacer-4) + 0.1rem);
                            &::before {
                                @include position($t: 50%, $l: 0);
                                @include pseudo();
                                width: .1rem;
                                height: 1.6rem;
                                background-color: var(--#{$prefix}gray-30);
                                transform: translateY(-50%);
                            }
                        }
                    }
                }
                &::after {
                    @include position($t: 0, $r: 0);
                    @include pseudo();
                    width: .1rem;
                    height: 100%;
                    background-color: var(--#{$prefix}gray-30);
                }
            }
            .main-tit-wrap {
                gap: var(--#{$prefix}spacer-4);
                .tit {
                    font-size: var(--#{$prefix}fz-title-md);
                }
            }
            #main-vb-links {
                @include flex-layout($fd: column, $ai: normal);
                gap: var(--#{$prefix}spacer-6);
                .main-d-links {
                    .btn-txt {
                        font-weight: map-get($fws, bold);
                        padding: var(--#{$prefix}spacer-5) var(--#{$prefix}spacer-6) !important;
                    }
                }
            }
        }
    }
}

//메인 컨텐츠 박스
.main-box-responsive {
    display: grid;
    grid-template-columns: (1fr 41rem);
    grid-gap: var(--#{$prefix}spacer-6);
    > div {
        @include flex-layout($fd: column, $ai: normal);
    }
    .d-auto {
        flex: 1;
    }
    .d-fix {
        width: 41rem;
    }
    .in {
        height: 100%;
        padding: var(--#{$prefix}spacer-8);
        border-radius: var(--#{$prefix}rd-12 );
        background-color: var(--#{$prefix}secondary-5);
        &.outline {
            gap: var(--#{$prefix}spacer-6);
            border: .1rem solid var(--#{$prefix}gray-40);
            background-color: var(--#{$prefix}white);
            .main-tit-wrap {
                @include flex-layout($jc: space-between);
                width: 100%;
                .tit {
                    font-size: var(--#{$prefix}fz-title-md);
                }
            }
        }
    }
    .swiper-in {
        display: table;
        table-layout: fixed;
        width: 100%;
    }
    &.type-wrap {
        > div {
            gap: var(--#{$prefix}spacer-10);
        }
    }    
}

//메인 섹션
.main-sect {
    margin-top: var(--#{$prefix}spacer-20);
    .inner {
        @include flex-layout($fd:column, $ai:normal);
        gap: var(--#{$prefix}spacer-10);
    }
    &.bg {
        padding-top: var(--#{$prefix}spacer-20);
        padding-bottom: var(--#{$prefix}spacer-20);
        background-color: var(--#{$prefix}secondary-5);
        .contents-wrap {
            .contents-text {
                background-color: #DFE7EF;
            }
        }
    }
    &:last-child {
        &:not(.bg) {
            padding-bottom: var(--#{$prefix}spacer-20);
        }
    }

    //tab
    .tab {
        @include flex-layout($ai:center); 
        .btn-txt {
            height: auto;
            &.ico-plus {
                margin-left: auto;
            }
        }
    }

    
    /* 자주찾는 메뉴 */
    //menu swiper
    .menu-list {
        @include flex-layout($fd: column, $ai: normal);
        gap: var(--#{$prefix}spacer-6);
        .menu-swiper-in {
            position: relative;
        }
        .menu-item {
            flex-shrink: 0;
            gap: var(--#{$prefix}spacer-4);
            .ico { 
                @include flex-layout($fd: column, $ai:center);
                flex-shrink: 0;
                width: 8rem;
                height: 8rem;
                background-repeat: no-repeat;
                background-position: center;
                &.i1 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico1.svg);
                }
                &.i2 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico2.svg);
                }
                &.i3 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico3.svg);
                }
                &.i4 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico4.svg);
                }
                &.i5 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico5.svg);
                }
                &.i6 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico6.svg);
                }
                &.i7 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico7.svg);
                }
                &.i8 {
                    background-image: url(#{$url}/pattern/content/main_menu_ico8.svg);
                }
            }
        }
        &:not(.outline) {
            .menu-item {
                .ico {
                    border-radius: var(--#{$prefix}rd-12);
                    background-color: var(--#{$prefix}secondary-5);
                }
            }
        }
        &.type1 {
            .menu-swiper-in {
                padding: 0 var(--#{$prefix}spacer-16);
            }
            .menu-item {
                @include flex-layout($fd: column, $ai:center);
                height: 11.6rem;
                .txt {
                    @include ellipsis(1);
                    width: 100%;
                    padding: 0 var(--#{$prefix}spacer-1);
                    text-align: center;
                }
            }
            &.outline {
                .menu-item {
                    justify-content: center;
                    height: 10.8rem;
                    border-radius: var(--#{$prefix}rd-12);
                    border: .1rem solid var(--#{$prefix}gray-40);
                    .ico {
                        width: 3.2rem;
                        height: 3.2rem;
                    }
                }
            }
        }
        &.type2 {
            .menu-swiper-in {
                padding: 0 var(--#{$prefix}spacer-26);
            }
            .menu-item {
                @include flex-layout();
                .tit {
                    font-weight: map-get($fws, bold);
                }
                .txt {
                    font-size: var(--#{$prefix}fz-body-sm);
                    color: var(--#{$prefix}gray-70);
                    margin-top: var(--#{$prefix}spacer-2);
                    @include ellipsis(2);
                }
                .ico {
                    &.i1 {
                        background-image: url(#{$url}/pattern/content/main_menu_blue_ico1.svg);
                    }
                    &.i2 {
                        background-image: url(#{$url}/pattern/content/main_menu_blue_ico2.svg);
                    }
                    &.i3 {
                        background-image: url(#{$url}/pattern/content/main_menu_blue_ico3.svg);
                    }
                    &.i4 {
                        background-image: url(#{$url}/pattern/content/main_menu_blue_ico3.svg);
                    }
                }
            }
        }
    }

    //menu list
    .menu-list-rel {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-gap: var(--#{$prefix}spacer-6);
        li {
            > .btn-txt {
                @include flex-layout($jc:space-between, $ai: center,);
                font-size: var(--#{$prefix}fz-title-md);
                font-weight: map-get($fws, bold);
                width: 100%;
                height: 7.2rem;
                padding: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-8) !important;
                border-radius: var(--#{$prefix}rd-12);
                background-color: var(--#{$prefix}secondary-5);
            }
        }
        .li1 {
            grid-column: 1 / 3;
        }
        .li2 {
               grid-column: 3 / 5;
        }
        .d-card {
            padding: var(--#{$prefix}spacer-8);
        }
        &.outline {
            li {
                > .btn-txt {
                    border: 0.1rem solid var(--#{$prefix}gray-40);
                    background-color: var(--#{$prefix}white);
                }
            }
        }
    }

    /* 복합 콘텐츠 */
    .contents-wrap {
        @include flex-layout($jc: normal);
        gap: var(--#{$prefix}spacer-6);
        .contents-text {
            @include flex-layout($fd: column,);
            gap: var(--#{$prefix}spacer-6);
            flex-shrink: 0;
            width: 30.2rem;
            height:47.6rem;
            padding: var(--#{$prefix}spacer-10);
            border-radius: var(--#{$prefix}rd-12);
            background-color: var(--#{$prefix}secondary-5);
            background-image: url(#{$url}/pattern/content/main_ico_book_M.svg);
            background-position: bottom 4rem right 4rem;
            background-repeat: no-repeat;
            .tit {
                font-size: var(--#{$prefix}fz-title-xlg);
                font-weight: map-get($fws, bold);
            }
            .txt {
                font-size: var(--#{$prefix}fz-body-md);
            }
        }
        .contents-list {
            @include flex-layout($fd: column, $ai: normal, $jc: space-between);
            gap: var(--#{$prefix}spacer-10);
            flex: 1;
            overflow: hidden;
            .contents-swiper-in {
                position: relative;
                z-index: 1;
                width: 100%;
                padding: 0 72px;
            }
            .swiper {
                height:39.6rem;
            }
        }
        
        .d-card {
            gap: var(--#{$prefix}spacer-10);
            .im {
                flex-shrink: 0;
                height: 19.6rem;
                border-radius: var(--#{$prefix}rd-12);
                background-repeat: no-repeat;
                background-position: center;
                &.i1 {
                    background-color: #E5E2EF;
                    background-image: url(#{$url}/pattern/content/main_ico_bag_S.svg);
                }
                &.i2 {
                    background-color: #CEE4EE;
                    background-image: url(#{$url}/pattern/content/main_ico_bankbook_S.svg);
                }
                &.i3 {
                    background-color: #CEE7E5;
                    background-image: url(#{$url}/pattern/content/main_ico_store_S.svg);
                }
            }
        }
    }

    /* 정책소개 */
    .policy-list {
        @include flex-layout();
        gap: var(--#{$prefix}spacer-6);
        > li {
            flex: 1;
            height:45.4rem;
        }
        .d-card {
            padding: 0;
            .im {
                flex-shrink: 0;
                height: 19.6rem;
                background-color: #DFE8F4;
                background-repeat: no-repeat;
                background-position: center;
                &.i1 {
                    background-image: url(#{$url}/pattern/content/main_ico_card_S.svg);
                }
                &.i2 {
                    background-image: url(#{$url}/pattern/content/main_ico_energy_S.svg);
                }
                &.i3 {
                    background-image: url(#{$url}/pattern/content/main_ico_bulb_S.svg);
                }
            }
            .in {
                padding: var(--#{$prefix}spacer-6);
                background-color: var(--#{$prefix}white);
            }
        }
    }

    /* 뉴스 */
    .news-list {
        @include flex-layout();
        flex-wrap: wrap;
        gap: var(--#{$prefix}spacer-6);
        > li {
            @include flex-layout();
            flex-wrap: wrap;
            gap: var(--#{$prefix}spacer-6);
            width: calc(50% - 1.2rem);
            height: 23rem;
            > a {
                display: block;
                height: 100%;
            }
        }
        .d-card {
            gap: 0 var(--#{$prefix}spacer-10);
            padding: var(--#{$prefix}spacer-8);
            .im {
                flex: 1;
                width: 100%;
                min-width: 10rem;
                max-width: 20rem;
                height: 100%;
                overflow: hidden;
                img {
                    min-width: 100%;
                    max-width: inherit;
                    height: 100%;
                }
            }
        }
    }

    /* 민원 서비스 */
    .service-list {
        @include flex-layout($fd: column, $ai: normal);
        gap: var(--#{$prefix}spacer-10);
        padding-left:.2rem;
        .swiper-slide {
            height: auto;
        }
    }

    /* 원스톱서비스 */
    .one-serv-wrap {
        @include flex-layout();
        gap: var(--#{$prefix}spacer-10);
        .main-d-links {
            width: 24rem;
        }
    }

    /* 공지사항 */
    .news-tbl-list {
        @include flex-layout($fd:column, $ai:normal);
        gap: var(--#{$prefix}spacer-4);
        width: 100%;
        margin-top: auto;
        li {
            @include flex-layout($jc: space-between);
            gap: var(--#{$prefix}spacer-4);
            .tit {
                @include ellipsis(1);
            }
            .date {
                flex-shrink: 0;
                color: var(--#{$prefix}gray-70);
            }
        }
    }

    /* 공지알림 */
    .main-noti-wrap {
        @include flex-layout($fd:column, $ai:normal);
        gap: var(--#{$prefix}spacer-6);
        .noti-primary-item {
            @include flex-layout($ai:center);
            gap: var(--#{$prefix}spacer-4);
            .tit {
                flex: 1;
                @include ellipsis(1);
            }
            .date {
                color: var(--#{$prefix}gray-70);
                margin-left: auto;
            }
        }
        .noti-swiper {
            &.no-swiper {
                .swiper-wrapper {
                    @include flex-layout();
                    gap: var(--#{$prefix}spacer-6);
