	.login-wrap {
		position: relative;
		width: 49rem;
		padding-right: var(--#{$prefix}spacer-10);
		@at-root .link-group {
			@include flex-layout($ai: center, $jc: center);
			> li {
				.btn {
					&.btn-txt {
						height: 4rem;
						padding: 0 1rem;
					}
				}
				& + li {
					position: relative;
					margin-left: var(--#{$prefix}spacer-2);
