        padding: var(--#{$prefix}spacer-8) 0;
        border-bottom: .1rem solid var(--#{$prefix}gray-40);
        .keyword {
            font-weight: map-get($fws, bold);
            color:var(--#{$prefix}primary-60);
            background-color:var(--#{$prefix}primary-10);
        }
        &:first-child {
            padding-top: 0;
        }
    }
    .info-top {
        display: flex;
        gap: var(--#{$prefix}spacer-4);
        .i-date {
            color: var(--#{$prefix}gray-70);
        }
    }
    .info-body {
        display: flex;
        flex-direction: column;
        gap: var(--#{$prefix}spacer-6);
        a {
            &:hover {
                text-decoration: underline;
            }
        }
        .tit {
            font-size: var(--#{$prefix}fz-body-lg);
            font-weight: map-get($fws, bold);
            margin-bottom: var(--#{$prefix}spacer-6);
        }
        .txt {
            color: var(--#{$prefix}gray-70);
            @include ellipsis(2);
        }
        .in {
            display: flex;
            gap: var(--#{$prefix}spacer-10);
        }
        .thum {
            display: inline-flex;
            flex-shrink: 0;
            width: 18rem;
            height: 11rem;
            border-radius: var(--#{$prefix}rd-8);
            overflow: hidden;
            img {
                width: 100%;
                height: 100%;
            }
        }
    }
    .info-btm {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: var(--#{$prefix}spacer-4);
        .url-link {
            text-decoration: underline;
        }
        .i-cate {
            display: inline-flex;
            align-items: center;
            gap: .2rem;
            color: var(--#{$prefix}gray-70);
            li {
                display: inline-flex;
                align-items: center;
                gap: .2rem;
                &:not(:first-child) {
                    &::before {
                        display: inline-flex;
                        content:'';
                        width:1.6rem;
                        height: 1.6rem;
                        background: url(#{$url}/component/common/ico_arr_16_right_gray70.svg) no-repeat center;
                        background-size: contain;
                    }
                }
            }
            .span {
                text-decoration: underline;
                padding : 0 .2rem;
                &:hover {
                    border-radius: var(--#{$prefix}rd-6);
                    background-color: var(--#{$prefix}secondary-5);
                    transition: $transition-base;
                }
                &:active {
                    background-color: var(--#{$prefix}secondary-10);
                    transition: $transition-base;
                }
            }
        }
        .btn-area {
            display: inline-flex;
            gap: var(--#{$prefix}spacer-2);
        }
    }
}

/* 통합검색 양쪽 배치 */
.search-between-conts {
    @include flex-layout();
    gap: var(--#{$prefix}spacer-20);
    .aside {
        flex-shrink: 0;
        .filter-list {
            border-bottom: .1rem solid var(--#{$prefix}gray-40);
        }
        .filter-dl {
            .dt {
                @include flex-layout($jc: space-between, $ai: center);
                font-size: var(--#{$prefix}fz-title-xlg);
                font-weight: map-get($fws, bold);
                line-height: 1;
                padding: var(--#{$prefix}spacer-4) 0;
                border-bottom: .1rem solid var(--#{$prefix}gray-70);
                .btn {
                    height: auto;
                }
            }
        }
        .filter-btn {
            @include flex-layout($ai: center);
            margin-top: var(--#{$prefix}spacer-6);
            .btn {
                flex: 1;
            }
        }
    }
    .contents {
        flex: 1;
    }
    .aside {
        width: 29.6rem;
    }
}

/* ** 신청하기 목록 ** */
/* 신청하기 검색 폼 */
.search-top-box {
    .sch-form-wrap {
        margin-bottom: var(--#{$prefix}spacer-10);
        .input-group {
            width: 48rem;
            .sch-input {
                flex: 1;
                .form-control {
                    width: 100%;
                }
            }
        }
        .sch-input {
            .form-control {
                width: 54.8rem;
            }
        }
    }

    .sch-filter-box {
        padding: var(--#{$prefix}spacer-10);
        border-radius: var(--#{$prefix}rd-12);
        background: var(--#{$prefix}secondary-5);
        .filter-form {
            @include flex-layout($ai: normal);
            gap: var(--#{$prefix}spacer-6);
            max-width: 100%;
            > div {
                @include flex-layout($d: inline-flex, $ai: center);
                gap: var(--#{$prefix}spacer-3);
                width: 100%;
                max-width: 25.5rem;
            }
            .label {
                font-weight: map-get($fws, bold);
                white-space: nowrap;
                min-width: 6.3rem;
            }
            .form-select {
                width: 100%;
                max-width: 18rem;
            }
            .form-control {
                width: 100%;
                max-width: 18rem;
            }
        }
        .filter-chip {
            @include flex-layout();
            gap: var(--#{$prefix}spacer-4);
            margin-top: var(--#{$prefix}spacer-6);
            padding-top: var(--#{$prefix}spacer-6);
            border-top: .1rem solid var(--#{$prefix}gray-40);
            dt {
                display: flex;
                align-items: center;
                font-weight: map-get($fws, bold);
                white-space: nowrap;
                height: 3.2rem;
                .num {
                    color: var(--#{$prefix}primary-60);
                }
            }
            dd {
                display: flex;
                gap: var(--#{$prefix}spacer-2);
                flex: 1;
                .ico-refresh {
                    flex-shrink: 0;
                }
                .chip-wrap {
                    display: flex;
                    flex-wrap: wrap;
                    justify-content: flex-start;
                    gap: var(--#{$prefix}spacer-2);
                }
            }
        }
    }
}

/* 신청하기 : 검색 결과 리스트 */
.search-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: var(--#{$prefix}spacer-6);
    .li {
        @include flex-layout($fd: column, $ai:normal);
        gap: var(--#{$prefix}spacer-6);
        padding: var(--#{$prefix}spacer-6);
        overflow-x: auto;
        box-sizing: border-box;
        border-radius: var(--#{$prefix}rd-12);
        border: .1rem solid var(--#{$prefix}gray-40);
        &.is-check {
            border-color: transparent;
            outline: .2rem solid var(--#{$prefix}primary);
        }
        .in {
            @include flex-layout($fd: column, $ai:normal);
            gap: var(--#{$prefix}spacer-6);
            width: 100%;
        }
    }
    .chk-area {
        z-index: 0;
        .form-check {
            display: inline-flex;
            input[type=checkbox]  {
                ~ label {
                    padding-left: var(--#{$prefix}spacer-5);
                }
            }
        }
    }
    .card-top {
        @include flex-layout($ai: center, $jc: space-between,);
        gap: var(--#{$prefix}spacer-6);
        .btn-area {
            display: inline-flex;
            gap: var(--#{$prefix}spacer-4);
            .btn {
                height: 2.4rem;
            }
        }
    }
    .card-body {
        @include flex-layout($fd: column, $ai: normal, );
        gap: var(--#{$prefix}spacer-6);
        .c-text {
            @include flex-layout($fd: column, $ai: normal, );
            gap: var(--#{$prefix}spacer-6);
            padding: var(--#{$prefix}spacer-4) 0;
            word-break: break-all;
            &:hover {
                text-decoration: underline;
            }
            .c-tit {
                @include flex-layout($ai: center,);
                gap: var(--#{$prefix}spacer-2);
                position: relative;
                font-size: var(--#{$prefix}fz-title-lg);
                font-weight: map-get($fws, bold);
                .span {
                    @include ellipsis(1);
                }
                &::after {
                    display: inline-flex;
                    flex-shrink: 0;
                    content: '';
                    width: var(--#{$prefix}spacer-6);
                    height: var(--#{$prefix}spacer-6);
                    background: url(#{$url}/component/common/ico_arr_20_right.svg) no-repeat;
                    background-size: contain;
                    background-position: center
                }
            }
            .c-txt {
                @include ellipsis(3);
            }
            .c-date {
                .key {
                    font-weight: map-get($fws, bold);
                }
            }
        }
        .c-btn {
            text-align: right;
        }
    }
    .card-btm {
        display: flex;
        flex-wrap: wrap;
        gap: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-1);
        padding-top: var(--#{$prefix}spacer-6);
        margin-top: auto;
        border-top: .1rem solid var(--#{$prefix}gray-30);
        .tag {
            @include flex-layout($d: inline-flex, $ai: center,);
            font-size: var(--#{$prefix}fz-label-sm);
            color: var(--#{$prefix}gray-80);
            height: 3.2rem;
            padding: 0 var(--#{$prefix}spacer-3);
            border-radius: 10rem;
            border: .1rem solid var(--#{$prefix}gray-40);
            background-color: var(--#{$prefix}white);
            &::before {
                content:'#';
            }
        }
    }
    &.type2 {
        .card-body {
            .c-tit {
                margin-top: 0;
            }
            .c-txt {
                .key {
                    margin-right: .6rem;
                    white-space: nowrap
                }
            }
            .c-txt-p {
                @include flex-layout();
                &:not(:first-child) {
                    margin-top: var(--#{$prefix}spacer-4);
                }
             }
            .c-txt-ul {
                @include flex-layout();
                flex-wrap: wrap;
                gap: var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-2);
                li {
                    display: inline-flex;
                    align-items: center;
                    &:not(:last-child) {
                        &::after {
                            display: inline-flex;
                            content:'';
                            width: .1rem;
                            height: 1.2rem;
                            margin-left: var(--#{$prefix}spacer-2);
                            background-color: var(--#{$prefix}gray-40);
                        }
                    }
                }
            }
        }
    }
}

.tbl-wrap {
    .def-list {
        .btn-txt {
            &.sm {
                height: 2.4rem;
            }
        }
    }
}

/* 신청하기 : 검색 결과 리스트 : 선택형 */
.page-appl-btn {
    @include flex-layout($fd:column, $ai: normal);
    gap: var(--#{$prefix}spacer-4);
    flex: 1;
    .page-appl-in {
        @include flex-layout();
        gap: var(--#{$prefix}spacer-4);
        .secondary {
            flex:1;
            + .primary {
                max-width: 15.2rem;
            }
        }
    }
    .page-appl-list {
        @include flex-layout($fd:column, $ai: normal);
        gap: var(--#{$prefix}spacer-4);
        padding: var(--#{$prefix}spacer-4);
        border-radius: .8rem;
        border: .1rem solid var(--#{$prefix}primary);
        background: var(--#{$prefix}white);
        li {
            @include flex-layout($jc: space-between, $ai: center);
            gap: var(--#{$prefix}spacer-4);
            height: 4.8rem;
            .txt {
                @include flex-layout($ai: center);
                flex: 1;
                gap: var(--#{$prefix}spacer-2);
                overflow: hidden;
                .krds-badge {
                    font-weight: map-get($fws, normal);
                }
                .span {
                    font-weight: map-get($fws, bold);
                    @include ellipsis(1);
                }
            }
            .btn-txt {
                flex: none;
            }
        }
    }
}

/* ** 바구니 ** */
.txt-box {
    .conts-desc-wrap {
        .krds-drop-wrap {
            &[data-type="info"] {
                display: flex;
                .drop-menu {
                    max-width: 100%;
                }
            }
        }
    }
}


/* ** 서비스 신청 : 상세 ** */
.conts-area {
	&.quick-nav-type {
		@include flex-layout($jc: space-between, $fd: row);
		gap: 0;
		> .conts-detail-wrap {
			@include flex-layout($fd: column);
			gap: var(--#{$prefix}spacer-18);
			width: 90.4rem;
            margin-right: 4rem;
			.quick-nav-area {
				width: 100%;
			}
		}
		> .quick-nav-area {
			@include flex-layout($fd: column);
			gap: var(--#{$prefix}spacer-10);
			@include position($p: sticky, $t: 24.4rem);
			width: 29.6rem;
			transition: $transition-base;
			> * {
				width: 100%;
			}
			.btn-wrap {
				.btn {
					width: 100%;
				}
			}
		}
		.quick-nav-tab {
			width: calc(100% + 3.2rem);
			margin: 0 -1.6rem;
			overflow: hidden;
			[role="tablist"] {
				padding-left: var(--#{$prefix}spacer-4);
				padding-right: var(--#{$prefix}spacer-4);
			}
		}
	}
}
.scroll-down {
	.conts-area {
		&.quick-nav-type {
			> .quick-nav-area {
				top: 6rem;
			}
		}
	}
}

.quick-title-wrap {
	.guide-txt {
		padding: 0 0 var(--#{$prefix}spacer-4) 0;
		background-color: transparent;
		border-radius: 0;
		font-size: var(--#{$prefix}fz-title-xsm);
	}
}

.sec-tit {
	font-size: var(--#{$prefix}fz-title-xlg);
	font-weight: map-get($fws, bold);
}

.quick-list {
	li {
		> a {
			display: block;
			position: relative;
			padding: .7rem 0 .7rem 2.6rem;
			transition: $transition-base;
			&.active {
				background-color: var(--#{$prefix}secondary-5);
				color: var(--#{$prefix}secondary);
				font-weight: map-get($fws, bold);
				&::before {
					width: .4rem;
					background-color: var(--#{$prefix}secondary);
				}
			}
			&::before {
				@include position($t: 0, $l: 0);
				@include pseudo();
				width: .2rem;
				height: 100%;
				background-color: var(--#{$prefix}secondary-10);
				transition: $transition-base;
			}
			&:focus {
				@include focus($offset: .4rem);
			}
		}
	}
}

.btn-wrap {
	.guide-txt {
		padding: var(--#{$prefix}spacer-4) 0 0 0;
		background-color: transparent;
		border-radius: 0;
		text-align: center;
	}
}

/* 상세 버튼 모음 */
.info-list {
	&.btn-txt-list {
		> li {
			+ li {
				margin-top: var(--#{$prefix}spacer-6) !important;
			}
		}
		.btn-txt {
            &.sm {
                height: auto;
            }
        }
	}
}

//contents depth guide
.conts-wrap {
	@include flex-layout($fd: column);
	gap: var(--#{$prefix}spacer-18);
	width: 100%;
	color: var(--#{$prefix}gray-70);
	&.scroll-check {
		gap: 0;
		.section-link {
			+ .section-link {
				padding-top: var(--#{$prefix}spacer-16);
			}
		}
	}
	> * {
		width: 100%;
	}
	.conts-wrap {
		gap: var(--#{$prefix}spacer-10);
		.conts-wrap {
			gap: var(--#{$prefix}spacer-8);
			.sec-tit {
				font-size: var(--#{$prefix}fz-title-md);
			}
			.conts-wrap {
				gap: var(--#{$prefix}spacer-6);
				.sec-tit {
					font-size: var(--#{$prefix}fz-title-sm);
				}
			}
		}
	}
	.sec-tit {
		color: var(--#{$prefix}gray-90);
	}
}


.calc-wrap {
	.total-txt {
		+ .calc-list {
			margin-top: var(--#{$prefix}spacer-3);
			padding-top: var(--#{$prefix}spacer-3);
			border-top: 1px dashed var(--#{$prefix}gray-40);
		}
	}
}


//컨텐츠 영역 확장 축소
.conts-expand-area {
	.expand-wrap {
		position: relative;
		overflow: hidden;
		transition: max-height .5s cubic-bezier(0, 1, 0, 1);
		&::after {
			@include position($b: 0, $l: 0);
			@include pseudo();
			width: 100%;
			height: 6.2rem;
			background: linear-gradient(180deg, transparent 0%, var(--#{$prefix}white) 100%);
			@include visibility($v: visible);
			z-index: 10;
			transition: $transition-base;
		}
	}
	.btn-conts-expand {
		@include flex-layout($ai: center);
		position: relative;
		border-radius: 0;
		&::after {
			@include pseudo();
			width: 2rem;
			height: 2rem;
			margin-left: var(--#{$prefix}spacer-1);
			background: url(#{$url}/pattern/content/ico_arr_expand.svg) no-repeat center;
			background-size: contain;
			transition: $transition-base;
			transform: rotate(0);
		}
	}
	&.active {
		.expand-wrap {
			max-height: 1000vh !important;
			transition: max-height 1.5s ease-in-out;
			&::after {
				@include visibility($v: hidden);
			}
		}
		.btn-conts-expand {
			&::after {
				transform: rotate(180deg);
			}
		}
	}
	//step button style
	&.step {
		.expand-wrap {
			max-height: 20.7rem;
		}
		.btn-conts-expand {
			height: 4rem;
			padding-left: 6.6rem;
			&::before {
				@include position($t:50%, $l: 1.2rem);
				@include pseudo();
				width: 2.4rem;
				height: 2.4rem;
				background: var(--#{$prefix}secondary-10) url(#{$url}/pattern/content/ico_conts_expand.svg) no-repeat center;
				background-size: 1.6rem;
				border-radius: var(--#{$prefix}rd-full);
				transform: translateY(-50%);
			}
		}
	}
	&.txt-box {
		padding-bottom: 0;
		.expand-wrap {
			max-height: 20rem;
			&::after {
				background: linear-gradient(180deg, transparent 0%, var(--#{$prefix}gray-5) 100%);
			}
		}
		.btn-conts-expand {
			justify-content: center;
			width: 100%;
			height: 6.2rem;
			border-top: 1px solid var(--#{$prefix}gray-40);
		}
	}
}

.service-step {
	> li {
		@include flex-layout($fd: column);
		gap: var(--#{$prefix}spacer-4);
		position: relative;
		padding-left: 6.6rem;
		padding-bottom: var(--#{$prefix}spacer-6);
		.num {
			@include flex-layout($ai: center, $jc: center);
			@include position($t:0, $l: 1.2rem);
			z-index: 2;
			width: 2.4rem;
			height: 2.4rem;
			background-color: var(--#{$prefix}secondary);
			border-radius: var(--#{$prefix}rd-full);
			color: var(--#{$prefix}white);
		}
		&::before {
			@include position($t: 0, $l: 2.4rem);
			@include pseudo();
			width: .1rem;
			height: 100%;
			background-color: var(--#{$prefix}secondary-20);
		}
		&:last-of-type {
			padding-bottom: 0;
			&::before {
				display: none;
			}
		}
		.tit-area {
			display: flex;
			width: 100%;
			.tit {
				color: var(--#{$prefix}secondary);
				font-weight: map-get($fws, bold);
			}
			.period {
				position: relative;
				margin-left: var(--#{$prefix}spacer-2);
				padding-left: calc(var(--#{$prefix}spacer-2) + 0.1rem);
				&::before{
					@include position($t: 50%, $l: 0);
					@include pseudo();
					width: .1rem;
					height: .8rem;
					background-color: var(--#{$prefix}gray-40);
					transform: translateY(-50%);
				}
			}
		}
		.conts-desc {

			width: 100%;
		}
		.btn-txt {
            &.sm {
                height: auto;
            }
        }
	}
}

.txt-box {
	padding: var(--#{$prefix}spacer-10);
	background-color: var(--#{$prefix}gray-5);
	border: 1px solid var(--#{$prefix}gray-40);
	border-radius: var(--#{$prefix}rd-12);
	color: var(--#{$prefix}gray-90);
	.conts-desc {
		padding-bottom: var(--#{$prefix}spacer-6);
	}
}


//box list style
.box-group-area {
	@include flex-layout($fd: column);
	gap: var(--#{$prefix}spacer-4);
	> li {
		@include flex-layout($ai: center, $jc: space-between);
		width: 100%;
		min-height: 6.4rem;
		padding: var(--#{$prefix}spacer-3) var(--#{$prefix}spacer-4);
		border: 1px solid var(--#{$prefix}gray-40);
		border-radius: .6rem;
		color: var(--#{$prefix}gray-90);
		.btn-wrap {
			width: auto;
			padding-left: var(--#{$prefix}spacer-6);
		}
	}
}



/* 등본 발급 */
.outline {
    .conts-desc-wrap {
        &.type1 {
            @include flex-layout($fd: column, $ai: normal,);
            gap: var(--#{$prefix}spacer-8);
            .sec-tit {
                font-size: var(--#{$prefix}fz-title-md);
            }
            .info-list {
                @include flex-layout($fd: column, $ai: normal,);
                gap: var(--#{$prefix}spacer-6);
                li {
                    margin: 0;
                }
            }
        }
    }
}

.fieldset-msg {
    position: relative;
    padding-left: var(--#{$prefix}spacer-6);
    font-size: var(--#{$prefix}fz-body-lg);
    color: var(--#{$prefix}primary-60);
    margin-bottom: var(--#{$prefix}spacer-6);
    &::before {
        @include position($t: .4rem, $l: 0);
        @include pseudo();
        width: 1.6rem;
        height: 1.6rem;
        background: url(#{$url}/pattern/content/ico_outline_chk.svg) no-repeat center;
        background-size: contain;
    }
}

.txt-box {
    &.bg-gray {
        border: 0;
        //박스 하단 버튼
        .btm-btn-wrap {
            @include flex-layout($jc: flex-end);
            gap: var(--#{$prefix}spacer-4);
            .btn {
                min-width: 9.1rem;
            }
        }
    }
    &.bg-white {
        @include flex-layout($fd:column, $ai:normal);
        gap: var(--#{$prefix}spacer-10);
        width: 100%;
        background-color: var(--#{$prefix}white);
        //box title
        .box-tit-wrap {
