        }
    }
    .search-total-top {
        position: sticky;
        top: 0;
        padding-bottom:var(--#{$prefix}spacer-10);
        border-bottom: .1rem solid var(--#{$prefix}gray-40);
        background-color: #DEE2E8;
    }

    .search-text {
        padding-top: var(--#{$prefix}spacer-10);
        .sch-info-list {
            display:flex;
            width: 100%;
            .li {
                @include flex-layout($d: inline-flex, $fd: column, $ai: flex-start,);
                flex: 1;
                width: 50%;
                min-height: 43rem;
                padding: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-6);
                margin:0;
                .tit {
                    font-size: 2.1rem;
                    font-size: var(--#{$prefix}fz-title-lg);
                    color:var(--#{$prefix}gray-90);
                    width: 100%;
                    margin-bottom: var(--#{$prefix}spacer-10);
                }
                .list {
                    @include flex-layout($fd: column, $ai: normal,);
                    gap: var(--#{$prefix}spacer-6);
                    width: 100%;
                    li {
                        @include flex-layout($ai: center,);
                    }
                }
                .ranking-txt {
                    @include ellipsis(1);
                }
                .ranking-state {
                    @include flex-layout($d: inline-flex, $jc: center, $ai: center,);
                    flex-shrink: 0;
                    gap:.2rem;
                    font-size: 1.5rem;
                    font-size: var(--#{$prefix}fz-body-sm);
                    width: 3.2rem;
                    &::before {
                        display: inline-flex;
                        content: '';
                        width: 1rem;
                        height: 1rem;
                    }
                    &.up {
                        color: var(--#{$prefix}danger);
                        &::before {
                            width: 0;
                            height: 0;
                            border-bottom: .6rem solid var(--#{$prefix}danger);
                            border-left: .4rem solid transparent;
                            border-right: .4rem solid transparent;
                        }
                    }
                    &.down {
                        color:var(--#{$prefix}information);
                        &::before {
                            width: 0;
                            height: 0;
                            border-top: .6rem solid var(--#{$prefix}information);
                            border-left: .4rem solid transparent;
                            border-right: .4rem solid transparent;
                        }
                    }
                    &.same {
                        &::before {
                            width:.6rem;
                            height:.2rem;
                            background-color:var(--#{$prefix}gray-90);
                        }
                    }
                }
            }
            .li1 {
                counter-reset: item;
                li {
                    gap: var(--#{$prefix}spacer-2);
                    counter-increment: item;
                    &::before {
                        display: inline-flex;
                        content: counter(item);
                        width: 2rem;
                    }
                    .ranking-state {
                        margin-left: auto;
                    }
                }
            }
            .li2 {
                border-left: .1rem solid var(--#{$prefix}gray-40);
                li {
                    gap: var(--#{$prefix}spacer-2);
                }
                .word-txt {
                    @include ellipsis(1);
                }
                .btn-ico {
                    &.ico-del {
                        flex-shrink: 0;
                        margin-left: auto;
                        background-image: url(#{$url}/component/common/ico_del_20.svg);
                    }
                }
                .btn-txt {
                    margin-top: auto;
                    &.ico-del {
                        &::after {
                            background-image: url(#{$url}/component/common/ico_del_20.svg);
                        }
                    }
                }
            }
        }
        .sch-keyword-list {
            @include flex-layout($fd: column, $ai: normal,);
            gap: var(--#{$prefix}spacer-6);
            .li {
                @include flex-layout($jc:space-between, $ai: center,);
                gap: var(--#{$prefix}spacer-2);
                word-break: break-all;
                .text {
                    @include flex-layout($fd: column,);
                    flex: 1;
                    gap: var(--#{$prefix}spacer-3);
                    .txt {
                        width: 100%;
                        @include ellipsis(1);
                    }
                    .url {
                        text-decoration: underline;
                    }
                }
                .btn-txt {
                    height: auto;
                }
            }
        }
    }
}

/* 신청하기 필터 */
.pop-filter-top {
    position: sticky;
    top: 0;
    left: -1.6rem;
    z-index: 4;
    padding: var(--#{$prefix}spacer-5) var(--#{$prefix}spacer-4);
    margin:0 -1.6rem;
    border-top: .1rem solid var(--#{$prefix}gray-70);
    background-color: var(--#{$prefix}secondary-5);
    .filter-tag-wrap {
        @include flex-layout();
        gap: var(--#{$prefix}spacer-2);
        .ico-refresh {
            flex-shrink: 0;
            margin: var(--#{$prefix}spacer-1) 0;
        }
        .tag-in {
            display: flex;
            flex-wrap: nowrap;
            gap: var(--#{$prefix}spacer-2);
            padding: var(--#{$prefix}spacer-1) var(--#{$prefix}spacer-10) var(--#{$prefix}spacer-1) 0;
            overflow: hidden;
            overflow-x: auto;
            &::-webkit-scrollbar {  
                height: .5rem;
            }
            &::-webkit-scrollbar-thumb {
                height: .1rem;
                background-color: var(--#{$prefix}gray-40);
                border-radius: 0rem; 
            }
            &::-webkit-scrollbar-track {
                background-color: var(--#{$prefix}gray-30);
            }
            &.is-open {
                flex-wrap: wrap;
                overflow-x: hidden;
                + .tag-btn {
                    padding-left: 0;
                }
            }
        }
        .tag-btn {
            display: inline-flex;
            position: absolute;
            top:0;
            right: 0;
            z-index: 2;
            padding: var(--#{$prefix}spacer-6) var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-6) var(--#{$prefix}spacer-8);
            background: linear-gradient(90deg, rgba(237, 241, 245, 0.30) 3.12%, #EDF1F5 46.25%);
        }
        .btn-ico {
            &.active {
                transform: rotate(-180deg);
            }
        }
    }

    .filter-dl {
        dt {
            font-weight: map-get($fws, bold);
            margin-bottom: var(--#{$prefix}spacer-4);
        }
        dd {
            font-size: var(--#{$prefix}fz-body-sm);
            color: var(--#{$prefix}gray-70);
        }
     }
}

