@include  tablet { 
    /* **  main ** */
    /* 메인 : 비주얼 영역 */
    .main-vban-wrap {
        .vb-swiper {
            .swiper-slide {
                height: 40rem;
                padding : 0 var(--#{$prefix}spacer-1) var(--#{$prefix}spacer-8);
                .text {
                    gap: var(--#{$prefix}spacer-8);
                }
            }
        }

        .vb-card {
            padding: var(--#{$prefix}spacer-12) var(--#{$prefix}spacer-10);
            .main-d-text {
                gap: var(--#{$prefix}spacer-8);
                min-height: 25rem;
            }
            .main-d-links {
                flex-wrap: wrap;
                position: relative;
                padding: 0;
                .btn {
                    height: 6rem;
                    padding : var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-6) !important;
                }
            }
        }

    }

    /* 메인 : 컨텐츠 영역 */
    .main-sect {
        .contents-wrap {
            .contents-text {
                width: 40%;
                max-width: 30.2rem;
            }
        }
        
        .menu-list {
            &.type2 {
                .menu-swiper-in {
                    padding: 0 var(--#{$prefix}spacer-16);
                }
            }
        }

        //정책소개
        .policy-list {
            flex-wrap: wrap;
            > li {
                &:not(:nth-child(3)) {
                    width: 50%;
                }
                &:nth-child(3) {
                    flex:none;
                    width: 100%;
                }
            }
        }

        .news-list {
            > li {
                height: 21rem;
            }
            .d-card {
                gap: 0 var(--#{$prefix}spacer-8);
                max-width: 100%;
                padding: var(--#{$prefix}spacer-6);
                .im {
                    img {
                        width: 100%;
                        height: auto;
                    }
                }
                .in {
                    display: table;
                    table-layout: fixed;
                    width: 100%;
                    .c-btn {
                        margin-top: var(--#{$prefix}spacer-6);
                    }
                }
            }
        }
        
        .menu-list-rel {
            grid-template-columns: 1fr 1fr;
            .li1 {
                grid-column: inherit;
            }
            .li2 {
                grid-column: inherit;
            }
        }
    }

    /* 메인 : 컨텐츠 레이아웃 */
    //카드타입 반응형
    .d-card.d-responsive {
        min-height: auto;
        .im {
            width: 40%;
        }
        &:not(.line) {
            .in {
                padding: 0 0 0 var(--#{$prefix}spacer-8);
            }
        }
        &.line {
            .im {
                width: 45%;
            }
        }
    }
    
    //컨텐츠 요소 박스 반응형
    .main-box-responsive {
        grid-template-columns: 1fr;
        height: auto !important;
        .d-fix {
            display: table;
            table-layout: fixed;
            width: 100%;
        }
        &.type-wrap {
            > div {
                gap: 0;
            }
            .main-tit-wrap {
                margin-bottom: var(--#{$prefix}spacer-8);
            }
        }
    }


    /* **  로그인 ** */

    //본인인증방식 선택
    .certify-sel-wrap {
        .certify-option {
            > li {
                flex-direction: column;
                gap: var(--#{$prefix}spacer-8);
            }
        }
        
        .certify-type {
            max-width: 100%;
        }
    }

    //로그인 정보 입력
    .login-form-area {
        flex-direction: column;
        padding-left: 0;
        padding-right: 0;
        .login-wrap {
            width: 100%;
            padding: 0;
            &::after {
                display: none;
            }
        }
    }

    //로그인 타입 선택
    .login-type-ul {
        flex-wrap: wrap;
        li {
            flex: none;
            &:nth-child(1) {
                padding-right: var(--#{$prefix}spacer-6);
            }
            &:nth-child(2) {
                padding-left: var(--#{$prefix}spacer-6);
                padding-right: 0;
            }
            &:not(:nth-child(3)) {
                align-items: center;
                width: 50%;
                padding-bottom: var(--#{$prefix}spacer-10);
                .btn {
                    flex-wrap: wrap;
                    padding: var(--#{$prefix}spacer-2) var(--#{$prefix}spacer-2);
                    min-height: 5.6rem;
                    height: auto;
                    gap: 0;
                    .text-bold {
                        display: flex;
                        justify-content: center;
                        width: 100%;
                    }
                }
            }
            &:nth-child(3) {
                width: 100%;
                padding-left: 0;
                padding-top: var(--#{$prefix}spacer-10);
                margin-left: 0;
                border-left: 0;
                border-top: .1rem solid var(--#{$prefix}gray-30);
                .in {
                    width: 100%;
                    max-width: 100%;
                }
            }
        }
    }



    /* ** 서비스 신청 ** */
    /* 신청 : 리스트 */
    .search-top-box {
        .sch-form-wrap {
            .sch-input {
                width: 70%;
                .form-control {
                    width: 100%;
                }
            }
        }
        .sch-filter-box {
            .filter-form {
                flex-wrap: wrap;
                > div {
                    width: 100%;
                    max-width: calc(50% - 1.2rem);
                }
                .form-select,
                .form-control {
                    width: 100%;
                    max-width: 100%;
                }
                .btn {
                    &.primary {
                        width: 100%;
                    }
                }
            }
        }
    }

    .search-list-top {
        &.type1,
        &.type2 {
            flex-direction: column;
            padding: var(--#{$prefix}spacer-8) 0;
            .sch-info {
                order: 2;
            }
            .sch-sort {
                order: 1;
            }
        }
    }

    .search-list {
        grid-template-columns: 1fr 1fr;
        .card-top {
            gap: 0;
            .btn-area {
                gap: var(--#{$prefix}spacer-2);
            }
        }
    }

    /* 신청 : 상세 */
    .conts-area {
		&.quick-nav-type {
			> .conts-detail-wrap {
                gap: var(--#{$prefix}spacer-16);
                > .quick-nav-area {
                    &.mob-only {
                        display: flex !important;
                    }
                    .quick-list {
                        width: 100%;
                    }
                }
			}
            > .quick-nav-area {
                &.pc-only {
                    display: none;
                }
            }
		}
	}
}

 
