    /* 로그인 방식 선택 */
    .login-type-ul {
        @include flex-layout($fd: column, $ai:normal);
        width: 100%;
        li {
            justify-content: flex-start;
            gap: var(--#{$prefix}spacer-8);
            padding:  var(--#{$prefix}spacer-8) 0;
            border: 0;
            border-top: 0.1rem solid var(--#{$prefix}gray-30);
            &:nth-child(1) {
                padding-top: 0;
                border: 0;
            }
            &:nth-child(3) {
                padding-bottom: 0;
            }
            .in {
                width: 100%;
                max-width: 100%;
            }
        }
    }

    /* 약관동의 */
    //약관동의 리스트
    .terms-agree-ul {
        li {
            padding: var(--#{$prefix}spacer-6) 0 0;
        }
        .agree-tit {
            gap: var(--#{$prefix}spacer-4);
        }
        .agree-box {
            @include flex-layout($fd:column,);
            gap: var(--#{$prefix}spacer-6);
            padding: var(--#{$prefix}spacer-5) var(--#{$prefix}spacer-4);
        }
    }


    /* 검색 공통 : 리스트 상단 */
    .search-list-top {
        flex-direction: column;
        gap: var(--#{$prefix}spacer-8);
        .sch-left {
            order: 2;
            flex-direction: column;
            align-items: flex-start;
        }
        .sch-info {
            order: 2;
        }
        .sch-sort {
            order: 1;
            li {
                &:not(:first-child) {
                    &::before {
                        display: none;
                    }
                }
            }
            .w-sort-btn {
                display: none;
            }
            .m-sort-btn {
                display: flex;
                align-items: center;
            }
            
        }
        .ico-filter {
            position: relative;
            font-size: var(--#{$prefix}fz-title-sm);
            height: 2.5rem;
            padding: .2rem;
            margin-left: var(--#{$prefix}spacer-4);
            .span {
                position: relative;
                z-index: 2;
                font-weight: map-get($fws, bold);
            }
            .num {
                display: inline-flex;
                position: absolute;
                right: .1rem;
                z-index: 1;
                height: 2rem;
                font-size: var(--#{$prefix}fz-label-sm);
                color:var(--#{$prefix}white);
                padding:0 var(--#{$prefix}spacer-2);
                margin-top: -3rem;
                border-radius: 2rem;
                background-color: var(--#{$prefix}primary);

            }
        }
        &.type1 {
            padding: 0 0 var(--#{$prefix}spacer-8);
        }
        &.type2 {
            padding-top: 0;
            margin-bottom: var(--#{$prefix}spacer-8);
        }
    }

    
    /* ** 검색 결과 페이지 ** */
    /* 검색어 영역*/
    .total-search-top-box {
        margin-bottom: var(--#{$prefix}spacer-10);
        > .inner {
            padding-top: var(--#{$prefix}spacer-10);
            padding-bottom: var(--#{$prefix}spacer-10);
        }
    }


    /* 통합검색 : 리스트 */
    .total-search-list {
        margin: 0 -1.6rem;
        .li {
            padding-left: var(--#{$prefix}spacer-4);
            padding-right: var(--#{$prefix}spacer-4);
        }
        .info-body {
            gap: var(--#{$prefix}spacer-8);
            .in {
                gap: var(--#{$prefix}spacer-8);
            }
            .txt {
                @include ellipsis(3);
            }
            .thum {
                width: 12rem;
                height: 7.4rem;
            }
        }
        .info-btm {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    /* ** 신청하기 검색 ** */

    /* 신청하기 검색 폼 */
    .search-top-box {
        margin-bottom: var(--#{$prefix}spacer-8);
        .sch-form-wrap {
            margin-bottom: var(--#{$prefix}spacer-8);
            .sch-input {
                width: 100%;
                .form-control {
                    width: 100%;
                    max-width: 100%;
                }
            }
            .input-group {
                width: 100%;
            }
        }

        .sch-filter-box {
            display: none;
        }
    }


    /* 신청하기 : 검색 결과 리스트 */
    .search-list {
        grid-template-columns: 1fr;
        .card-body {
            .c-btn {
                text-align: left;
            }
        }
        &.type2 {
            .card-body {
                .c-text {
                    padding: 0;
                }
                .c-txt-ul {
                    flex-wrap: wrap;
                }
            }
        }
    }

    /* 신청하기 : 상세 */
    .conts-area {
		&.quick-nav-type {
			> .conts-detail-wrap {
				gap: var(--#{$prefix}spacer-10);
				width: 100%;
			}
		}
	}

    //contents depth guide
	.conts-wrap {
		gap: var(--#{$prefix}spacer-10);
		&.scroll-check {
			gap: 0;
			.section-link {
				+ .section-link {
					padding-top: var(--#{$prefix}spacer-10);
				}
			}
		}
		.conts-wrap {
			gap: var(--#{$prefix}spacer-8);

		}
	}

    //아웃라인
    .outline {
		gap: var(--#{$prefix}spacer-8);
		.conts-desc-wrap {
			padding-top: var(--#{$prefix}spacer-8);
		}
	}

	//box list style
	.box-group-area {
		> li {
			flex-direction: column;
			align-items: flex-start;
			gap: var(--#{$prefix}spacer-4);
			.btn-wrap {
				width: 100%;
				padding-left: 0;
				text-align: left;
				.btn-txt {
					&.sm {
						height: auto;
					}
				}
			}
		}
	}

	//list형 checkbox
	.chk-group-area {
		.chk-all-wrap {
			flex-direction: column;
			align-items: flex-start;
			gap: var(--#{$prefix}spacer-1);
			.chk-num {
				width: 100%;
				text-align: right;
			}
		}
		.chk-group-wrap {
			@include flex-layout($fd: column);
			gap: var(--#{$prefix}spacer-6);
			> li {
				padding: var(--#{$prefix}spacer-6) var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-4);
			}
			$box-pl: 3.6rem;
			input[type=radio],
			input[type=checkbox] {
				+ label {
					padding-left: $box-pl;
				}
			}
			.btn-wrap {
				margin-left: $box-pl;
			}
		}
	}

	.txt-box {
		padding: var(--#{$prefix}spacer-6);
		.outline {
			padding:  var(--#{$prefix}spacer-8)  var(--#{$prefix}spacer-6);
		}
	}


    /* ** 신청안내 : 등본 발급 ** */
    .txt-box {
        &.bg-gray {
            //박스 하단 버튼
            .btm-btn-wrap {
                .btn {
                    flex: 1;
                }
            }
        }

        &.bg-white {
            gap: var(--#{$prefix}spacer-8);
            //box area
            .box-cnt {
                gap: var(--#{$prefix}spacer-10);
            }
            .box-tit-wrap {
                @include flex-layout($fd:column);
                gap: var(--#{$prefix}spacer-4);
            }

            .form-group-row {
                flex-direction: column;
                width: 100%;
            }

            .form-group {
                width: 100%;
            }
            .form-conts {
                width: 100%;
                > .input-group {
                    width: 100%;
                }
            }
            .form-check-cnt {
                width: 100%;
            }
            .form-group-addr {
                flex-direction: column;
                width: 100%;
            }
            .input-group {
                &.m-column {
                    flex-direction: column;
                    .btn {
                        width: 100%;
                    }
                }
            }
            .tbl-wrap {
                .tbl.data {
                    thead {
                        th {
                            word-break: break-all;
                            padding : var(--#{$prefix}spacer-2);
                        }
                    }
                    tbody {
                        td {
                            word-break: break-all;
                            padding : var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-2);
                        }
                    }
                }
            }
        }        

        //도서 상세 정보
        .book-info-ul {
            align-items: normal;
            > li {
                flex-direction: column;
                gap: var(--#{$prefix}spacer-4);
            }
        }
    }
 
    /* ** 완료 페이지 ** */
    // 완료 메세지
    .comp-msg-wrap {
        font-size: var(--#{$prefix}fz-heading-md);
    }
    
    //완료 정보
    .comp-info-box {
        padding: var(--#{$prefix}spacer-6);
        li {
            .key {
                width: 8rem;
            }
        }
    }

    //완료버튼
    .comp-btn-wrap {
        margin-top:var(--#{$prefix}spacer-8);
        @include group-wrap;
		.btn {
			@include group;
			padding: 0;
		}
    }
    
    //링크 버튼
    .comp-link-box {
        padding: var(--#{$prefix}spacer-6);
        dd {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    /* ** 신청안내 : 등본 발급 ** */
    .outline {
        .conts-desc-wrap {
            &.type1 {
                gap: var(--#{$prefix}spacer-7);
                .sec-tit {
                    font-size: var(--#{$prefix}fz-title-md);
                }
                .info-list {
                    gap: var(--#{$prefix}spacer-6);
                }
            }
        }
    }

    /* ** 평가 ** */
    .assess-question-wrap {
        align-items: flex-start;
        flex-direction: column;
        .assess-an {
            width: 100%;
            .form-chip {
                flex: 1;
                .radio {
                    ~ .form-chip-outline {
                        width: 100%;
                    }
                }
            }
        }
    }
    
    .assess-opinion-wrap {
        gap: var(--#{$prefix}spacer-7);
        padding-top: var(--#{$prefix}spacer-8);
        margin-top: var(--#{$prefix}spacer-8);
    }

    .assess-end-message {
        margin-top: var(--#{$prefix}spacer-16);
    }
 
 
    /* **** POPUP **** */
    /* 푸터 - 관련 기관 링크 */
    .pop-foot-links {
        grid-template-columns: 1fr 1fr;
        grid-gap: 0;
    }
 
    /* 통합검색 */
    .pop-total-serch-wrap {
        padding: var(--#{$prefix}spacer-10) 0;
        .search-tit {
            flex-direction: column;
            align-items: flex-start;
            gap: var(--#{$prefix}spacer-4); 
            .btn-txt {
                padding: 0;
                margin: 0;
            }
        }

        .search-total-top {
            padding: 0 var(--#{$prefix}spacer-4) var(--#{$prefix}spacer-8);
            margin: 0 -1.6rem;
        }
 
        .search-text {
            padding: var(--#{$prefix}spacer-8) var(--#{$prefix}spacer-4) 0;
            .sch-info-list {
                flex-direction: column;
                
                .li {
                    width: 100%;
                    min-height: auto;
                    padding: 0;
                    .tit {
                        margin-bottom: var(--#{$prefix}spacer-8);
                    }
                 }
                 .li2 {
                    padding-top: var(--#{$prefix}spacer-8);
                    margin-top: var(--#{$prefix}spacer-8);
                    border:0;
                    border-top: .1rem solid var(--#{$prefix}gray-40);
                    .btn-txt {
                        &.ico-del {
                            margin-top: var(--#{$prefix}spacer-8);
                        }
                    }
                }
            }
            .sch-keyword-list { 
                .li {
                    gap: var(--#{$prefix}spacer-4);
                    .text {
                        .url {
                            text-decoration: none;
                        }
                    }
                    .ico-go {
                        span {
                            @include sr-only;
                        }
                    }
                }
            }
        }
    }
}